<template>
  <div id="employee-table">
    <p v-if="employees.length < 1" class="empty-table">
      No employees
    </p>
    <table v-else>
      <thead>
        <tr>
          <th>Employee name</th>
          <th>Employee email</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="employee in employees" :key="employee.id">

          <td v-if="employee.id === editing">
            <input type="text" v-model="temporaryEmployee.name"/>
          </td>
          <td v-else>{{ employee.name }}</td>

          <td v-if="employee.id === editing">
            <input type="text" v-model="temporaryEmployee.email"/>
          </td>
          <td v-else>{{ employee.email }}</td>

          <td v-if="employee.id === editing">
            <button @click="editEmployee(employee)">Save</button>
            <button class="muted-button" @click="cancelEdit(employee)">Cancel</button>
          </td>
          <td v-else>
            <button @click="editMode(employee)" >Edit</button>
            <button @click="$emit('delete:employee', employee.id)" >Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    name: 'employee-table',
    props: {
        employees: Array,
    },
    data() {
      return {
        editing: null,
        temporaryEmployee: {},
        // cachedEmployee: {},
      }
    }, 
    methods: {
      editMode(employee) {
        // console.log("employees are: " + employees );
        this.temporaryEmployee = Object.assign({}, employee);
        this.cachedEmployee = Object.assign({}, employee);
        this.editing = employee.id;
      }, 
      
      editEmployee(employee) {
        if(this.temporaryEmployee.name === '' || this.temporaryEmployee.email === '') return ;
        this.$emit('edit:employee', employee.id, this.temporaryEmployee);
        this.editing = null;
      },

      cancelEdit(employee) {
        Object.assign(employee, this.cachedEmployee); 
        this.editing = null; 
      }
    }
  }
</script>

<style scoped>
  button {
    margin: 0 0.5rem 0 0;
  }
  </style>